.detail{
    display: flex;
    height: 250px;
    width: 150%;
    margin-left: 10%;
    color: #97bfff;
    padding-left: 5%;
    border-radius: 15px;
    background-image: url(../assets/stars.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    border: 5px solid chartreuse;
}

.backHome {
    text-align: center;
    border-radius: 999px;
    border: 2px solid black;
    background-color: rgb(250, 34, 106);
    color: rgb(209, 40, 96);
    font-weight: bold;
    width: 150px;
    height: 50px;
    box-shadow: 0 5px 2px #fe5353;
    margin-bottom: 5%; 
    margin-top: 5%;
    margin-left: 10%;
}
.backHome:hover {
    color: rgb(147, 0, 0);
    border: 6px solid black;
    transition: 1s;
    cursor: pointer;
}
.image {
    float: right;
    border-radius: 3%;
    width: 100%;
    height: 100%;
}