.imageContainer {
    display: flex;
    flex-direction: column;
    width: 50%;
    height: 60%;
    border-radius: 10px;
}
.btnDelete{
    margin: 5px;
    display: inline-flex;
    padding-right: 3%;
    align-items: center;
    justify-content: center;
}
.favContainer{
    display: flex;
    flex-direction: row-reverse;
    height: 50%;
    gap: 5px;
    background-color: #85dc91;
    width: 130%;
    margin-left: 25%;
    border-radius: 10px;
}
.infoFav{
    font-size: 24px;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    margin-right: 5%;
    text-align: center;
    font-weight: bold;
    color: #014932;
}  
.backHome {
    text-align: center;
    border-radius: 999px;
    border: 2px solid black;
    background-color: #039d58;
    color: #47bc57;
    font-weight: bold;
    width: 150px;
    height: 50px;
    box-shadow: 0 5px 2px #54fb00;
    margin-bottom: 5%; 
    margin-top: 2%;
}
.backHome:hover {
    color: rgb(2, 65, 1);
    border: 5px solid black;
    transition: 1s;
    cursor: pointer;
}